.my-learn {
  padding-top: 60px;
  width: 100%;
  background: #F6F7FB;
}
.my-learn .Space-pic {
  min-width: 1366px;
}
.my-learn .center-title {
  margin: 42px 0 15px 50px;
}
.my-learn .center-title span {
  font-size: 28px;
  line-height: 120.7%;
  color: #2A283D;
}
.my-learn .center-content {
  margin: 0 30px;
}
.my-learn .center-content .center-content-left {
  padding-right: 27px;
}
.my-learn .center-content .center-content-left .center-content-left-title {
  margin: 23px 30px 0 30px;
  color: #2A283D;
  font-size: 14px;
}
.my-learn .center-content .center-content-left .center-content-left-title .center-content-left-title-span {
  opacity: 0.4;
}
.my-learn .center-content .center-content-left .center-content-left-Vector {
  text-align: right;
  opacity: 1;
}
.my-learn .center-content .center-content-left .center-content-left-Vector .left {
  display: inline-block;
  height: 18px;
}
.my-learn .center-content .center-content-left .center-content-left-Vector .left img {
  margin-top: -2px;
}
.my-learn .center-content .center-content-left .center-content-left-Vector .left span {
  margin-left: 6px;
}
.my-learn .center-content .center-content-left .center-content-left-Vector .right {
  display: inline-block;
  padding: 0 5px;
  border-left: 1px solid #dddddd;
  margin-left: 20px;
  height: 18px;
}
.my-learn .center-content .center-content-left .center-content-left-Vector .right img {
  height: 18px;
  display: inline-block;
  margin: 0 10px;
  vertical-align: top;
}
.my-learn .center-content .center-content-left-content {
  margin: 0 30px;
  min-height: 500px;
}
.my-learn .center-content .center-content-left-content .ant-col-6 {
  margin: 25px 11.46% 25px 0;
  width: 16.4%;
  cursor: pointer;
}
.my-learn .center-content .center-content-left-content .ant-col-6 .pic-img {
  width: 100%;
  height: 153px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: url(../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.my-learn .center-content .center-content-left-content .ant-col-6 .pic-img img {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.my-learn .center-content .center-content-left-content .ant-col-6 .pic-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 10px;
  bottom: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.my-learn .center-content .center-content-left-content .ant-col-6 .pic-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
}
.my-learn .center-content .center-content-left-content .ant-col-6:hover .title1 {
  color: #06a5d8;
}
.my-learn .center-content .center-content-left-content .ant-col-6:nth-child(4n) {
  margin-right: 0;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-img {
  width: 100%;
  height: 100%;
  border-radius: 5.15756px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title {
  margin-top: 14px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .title1 {
  font-size: 18px;
  color: #2A283D;
  margin-bottom: 8px;
  height: 27px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .title2 {
  font-size: 12px;
  line-height: 16px;
  color: #2A283D;
  opacity: 0.4;
  margin-bottom: 4px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .title3 {
  font-size: 12px;
  line-height: 16px;
  color: #2A283D;
  opacity: 0.4;
  margin-bottom: 1px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .title4 {
  font-size: 12px;
  line-height: 16px;
  color: #2A283D;
  opacity: 0.4;
  margin-bottom: 6px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .Progress-content {
  display: inline-block;
  width: 36%;
  margin-left: 15px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .Progress-content .ant-progress-inner {
  background: #EAEAEA;
  border-radius: 5.91537px;
}
.my-learn .center-content .center-content-left-content .center-content-left-content-title .Progress-content .ant-progress-success-bg,
.my-learn .center-content .center-content-left-content .center-content-left-content-title .Progress-content .ant-progress-bg {
  background: linear-gradient(90deg, #1BDDEC 0%, #07BDF9 100%);
  box-shadow: 0px 4px 8px rgba(37, 136, 191, 0.34);
  border-radius: 5.73466px;
}
.my-learn .center-content .ant-card {
  border-radius: 5px;
  padding-bottom: 20px;
}
.my-learn .center-content .ant-card .ant-card-body {
  padding: 0;
}
.my-learn .center-content .fixed-center {
  position: fixed;
  right: 26px;
  min-width: 284px;
  margin-top: -44px;
}
.my-learn .center-content .fixed-center .center-content-right {
  margin: 0 18px 18px 8px;
}
.my-learn .center-content .fixed-center .center-content-right .center-content-right-title {
  font-size: 22px;
  line-height: 120.7%;
  color: #2A283D;
}
.my-learn .center-content .fixed-center .center-content-right .center-content-right-img {
