}
.my-learn .center-content .fixed-center .center-content-right .center-content-right-img span {
  color: #00BBF8;
  font-size: 14px;
  line-height: 120.7%;
  cursor: pointer;
}
.my-learn .center-content .fixed-center .center-content-right .center-content-right-img span img {
  margin-top: -2px;
  margin-right: 10px;
}
.my-learn .center-content .fixed-center .center-content-right-content {
  margin: 32px 0 10px 20px;
  cursor: pointer;
}
.my-learn .center-content .fixed-center .center-content-right-content .center-content-right-content-img {
  margin-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75px;
  width: 94px;
  position: relative;
  background: url(../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.my-learn .center-content .fixed-center .center-content-right-content .center-content-right-content-img img {
  width: 100%;
  height: 100%;
}
.my-learn .center-content .fixed-center .center-content-right-content .center-content-right-content-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 2px;
  bottom: 2px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.my-learn .center-content .fixed-center .center-content-right-content .center-content-right-content-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
  min-width: auto;
  min-height: auto;
}
.my-learn .center-content .fixed-center .center-content-right-content .right-title1 {
  font-size: 18.0844px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 7px;
}
.my-learn .center-content .fixed-center .center-content-right-content .right-title2 {
  color: #2A283D;
  font-size: 14px;
  opacity: 0.4;
  margin-bottom: 5px;
}
.my-learn .center-content .fixed-center .center-content-right-content .right-title2:last-child {
  margin-bottom: 0;
}
.my-learn .center-content .fixed-center .center-content-right-content:hover .right-title1 {
  color: #00BBF8;
}
.my-learn .center-content .fixed-center .no-content {
  min-height: 400px;
}
.my-learn .center-content .fixed-center .no-content .ant-empty {
  position: absolute;
  left: 40%;
  top: 30%;
  transform: translate(-30%, -50%);
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
